import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { ConfirmationService } from 'primeng/primeng';
import { ProductBean } from './bean/product.bean';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
// import { IsRcord, IsScene } from './../constant/codeValue';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as _ from 'lodash';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class Product implements OnInit {
  productBean: ProductBean = new ProductBean();
  ch: any = CALENDAR_CN;
  //table数据
  list: any = [];
  //传入详情的值
  inValueCode: string;
  totalNum: string;
  //模态框
  display: boolean = false;
  //1 更新 2添加 3详情
  showModel: string;
  //弹出框的头信息
  headerTitle: string;
  total: number;
  first: number = 0;//分页控制
  //提示信息
  msgs: Message[] = [];
  isRcord: any = [];
  IsScene: any = [];
  code: any;//获取所有码值
  list1: any = [];
  list2: any = [];
  list3: any = [];
  list4: any = [];
  date3: any = [];
  effDt: Date;
  invalDt: Date;
  invalDtStart: Date;
  invalDtEnd: Date;
  userform: FormGroup; // 校验 
  tabMesg: any;// 表格中无数据时显示的内容
  constructor(
    public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public router: Router,
    public fb: FormBuilder,
    public commfunc: Commfunc,
  ) {
    this.codeValues();
    this.IsScene = this.code['IsScene'];
   
    for(let i in  this.IsScene)
    {
      let mid=[]
      if(this.IsScene[i]['value']=='A3'){
        mid=this.IsScene[1];
        this.IsScene[1]=this.IsScene[i];
        this.IsScene[i]=mid
      } 
      if(this.IsScene[i]['value']=='A4'){
        mid=this.IsScene[2];
        this.IsScene[2]=this.IsScene[i];
        this.IsScene[i]=mid
      } 
      if(this.IsScene[i]['value']=='A5'){
        mid=this.IsScene[3];
        this.IsScene[3]=this.IsScene[i];
        this.IsScene[i]=mid
      }     
        
    }
    this.isRcord = this.code['IsRcord'];
    this.httpService.queryRateNegotionRuleListService(this.productBean).subscribe(data => {
      let arr = []
      if (data.resultList) {
        //   data.resultList.forEach((item) => {
        //     let obj = {};
        //     obj['label'] = item['plnRsetName'];
        //     obj['value'] = item['plnRsetNum'];
        //     arr.push(obj)
        //   })
        //   let a = _.clone(arr);
        //   this.list1 = a
        this.list1 = this.commfunc.toChangeTree(data.resultList, "plnRsetName", "plnRsetNum")
        this.list1.unshift({ label: '请选择', value: '' })
      }
    });
    // 利率计划名称  
    this.httpService.queryRatePlanInternalListService(this.productBean).subscribe(data => {
      let arr = []
      if (data.resultList) {
        data.resultList.forEach((item) => {
          let obj = {};
          obj['label'] = item['intRatePlnName'];
          obj['value'] = item['intRatePlnNum'];
          arr.push(obj)
        })
        let a = _.clone(arr);
        this.list4 = a;
        this.list4.unshift({ label: '请选择', value: '' })
      }
    })
    // 差异化编码名称
    this.httpService.queryRateDifferentRuleInternalListService(this.productBean).subscribe(data => {
      let arr = []
      if (data.resultList) {
        data.resultList.forEach((item) => {
          let obj = {};
          obj['label'] = item['prefPlnName'];
          obj['value'] = item['prefPlnNum'];
          arr.push(obj)
        })
        let a = _.clone(arr);
        this.list2 = a;
        this.list2.unshift({ label: '请选择', value: '' })
      }
    });

    // 周期重定价名称
    this.httpService.queryRateRepriceRuleInternalListService(this.productBean).subscribe(data => {
      let arr = []
      if (data.resultList) {
        data.resultList.forEach((item) => {
          let obj = {};
          obj['label'] = item['reprncPlnName'];
          obj['value'] = item['reprncPlnNum'];
          arr.push(obj)
        })
        let a = _.clone(arr);
        this.list3 = a;
        this.list3.unshift({ label: '请选择', value: '55' })
      }
    })
  }
  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    this.productBean.pageSize = 10;
    this.productBean.pageNum = 1;
    this.userform = this.fb.group({
      'pdName': new FormControl(''),
      'pdNum': new FormControl(''),
      'sroCode': new FormControl(''),
      'effDt': new FormControl(''),
      'invalDt': new FormControl(''),
      'invalDtStart': new FormControl(''),
      'invalDtEnd': new FormControl(''),
      'prefPlnNum': new FormControl(''),
      'reprncPlnNum': new FormControl(''),
      'rcrdStCode': new FormControl(''),
      'intRatePlnNum': new FormControl(''),
    });

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  querySecle() {
    this.productBean.pageSize = 10;
    this.productBean.pageNum = 1;
    this.first = 0;
    this.toquery();
  }
  toquery() {
    this.productBean.effDt = this.commfunc.transDateN(this.effDt);//生效日期
    this.productBean.invalDt = this.commfunc.transDateN(this.invalDt);//失效日期
    this.productBean.invalDtStart = this.commfunc.transDateN(this.invalDtStart);
    this.productBean.invalDtEnd = this.commfunc.transDateN(this.invalDtEnd);
    this.httpService.queryProductWithRateService(this.productBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        if (data.resultList == "" || data.resultList == null) {
          this.tabMesg = tableMessage
        }
        if (data.resultList) {
          this.list = data.resultList
        }
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.list = '';
      }
    }, error => {
      this.tabMesg = tableMessage
    })
  }

  //   if (data.resultList == "" || data.resultList == null) {
  //     this.tabMesg = tableMessage
  //   }
  //   if (data.resultList) {
  //     this.list = data.resultList
  //   }
  //   this.total = data.total;
  // }, err => {
  //   this.tabMesg = tableMessage
  // })
  // }
  //重置
  newSet() {
    this.productBean = new ProductBean();
    this.first = 0;
    this.list = [];
    this.userform.reset()
  }
  //添加
  add(param) {
    this.headerTitle = '产品与利率新增';
    this.router.navigate(['/pages/tzb/valuation/product/product-add'])
  }
  //新增的回调函数
  addCall(param) {
    this.display = param;
  }
  //详情
  detail(param) {
    this.headerTitle = '产品与利率详情';
    this.showModel = '2';
    this.display = true;
    this.inValueCode = param;
  }
  //详情的回调方法
  detailCall(param) {
    this.display = param;
  }
  //修改的回调函数
  updateCall(param) {
    this.display = false;
    if (param != false) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param });
    } this.toquery();
  }
  //修改
  update(param) {
    this.headerTitle = '产品与利率修改';
    this.showModel = '1';
    this.display = true;
    this.inValueCode = param;
    this.toquery();
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.productBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.productBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.productBean.pageSize;
    //调用查询的方法
    this.toquery();
  }
  //删除
  delete(car) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          pdNum: car.pdNum,
          intRatePlnNum: car.intRatePlnNum,
          sroCode: car.sroCode,
          effDt: car.effDt,
          id: car.id,
        };
        this.httpService.deleteProductWithRateService(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.toquery();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  //选择产品模板
  chooseProduct() {
    this.headerTitle = '可售产品列表';
    this.showModel = '3';
    this.display = true;
  }
  //选择产品模板
  chooseCall(parm) {
    if (parm) {
      this.productBean.pdNum = parm[0].productId;
      this.display = parm[1];
    } else {
      this.display = parm;
    }
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}


