import { Component, OnInit, Input, Output, EventEmitter, OnChanges, ChangeDetectorRef } from '@angular/core';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { ConfirmationService } from 'primeng/primeng';
import { ProductBean } from './../bean/product.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// import { IsScene, IsFazhi } from './../../constant/codeValue';
import * as _ from 'lodash';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { decimalInteger, decimalInt } from 'app/@uisftech/common/service/validator';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';

@Component({
  selector: 'app-product-update',
  templateUrl: './product-update.component.html',
  styleUrls: ['./product-update.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class ProductUpdateComponent implements OnInit {
  productBean: ProductBean = new ProductBean();
  date3;
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //传入详情的值
  inValueCode: string;
  //提示信息
  msgs: Message[] = [];
  ch: any = CALENDAR_CN;
  //模态框
  display: boolean = false;
  //1 更新 2添加 3详情
  showModel: string;
  // 弹出框的头信息
  headerTitle: string;
  totalNum: string;
  effDt: Date;
  invalDt: Date;
  old: string;
  old1: string;
  old2: string;
  old3: string;
  list: any = [];
  list1: any = [];
  list2: any = [];
  list3: any = [];
  isScene: any = [];
  isFazhi: any = [];
  code: any;//获取所有码值
  userform: FormGroup; //校验 
  decimalInteger = decimalInteger;
  decimalInt = decimalInt;
  isRed: boolean = false;
  show: boolean = false;
  show1: boolean = false;
  isDisable: boolean = false;
  show2: boolean = false;
  constructor(
    public httpService: ValuationHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
    public ce: ChangeDetectorRef) {
    this.codeValues();
    this.isScene = this.code['IsScene'];
    for(let i in  this.isScene)
    {
      let mid=[]
      if(this.isScene[i]['value']=='A3'){
        mid=this.isScene[1];
        this.isScene[1]=this.isScene[i];
        this.isScene[i]=mid
      } 
      if(this.isScene[i]['value']=='A4'){
        mid=this.isScene[2];
        this.isScene[2]=this.isScene[i];
        this.isScene[i]=mid
      } 
      if(this.isScene[i]['value']=='A5'){
        mid=this.isScene[3];
        this.isScene[3]=this.isScene[i];
        this.isScene[i]=mid
      }     
        
    }
    this.isFazhi = this.code['IsFazhi'];
    this.httpService.queryRatePlanInternalListService(this.productBean).subscribe(data => {
      let arr = []
      if (data.resultList) {
        data.resultList.forEach((item) => {
          let obj = {};
          obj['label'] = item['intRatePlnName'];
          obj['value'] = item['intRatePlnNum'];
          arr.push(obj)
        })
        let a = _.clone(arr);
        this.list = a
      }
    })
    this.httpService.queryRateNegotionRuleListService(this.productBean).subscribe(data => {
      let arr = []
      if (data.resultList) {
        data.resultList.forEach((item) => {
          let obj = {};
          obj['label'] = item['plnRsetName'];
          obj['value'] = item['plnRsetNum'];
          arr.push(obj)
        })
        let a = _.clone(arr);
        this.list1 = a;
        this.list1.unshift({ label: '请选择', value: '' })
      }
    })
    this.httpService.queryRateDifferentRuleInternalListService(this.productBean).subscribe(data => {
      let arr = []
      if (data.resultList) {
        data.resultList.forEach((item) => {
          let obj = {};
          obj['label'] = item['prefPlnName'];
          obj['value'] = item['prefPlnNum'];
          arr.push(obj)
        })
        let a = _.clone(arr);
        this.list2 = a;
        this.list2.unshift({ label: '请选择', value: '' })
      }
    })
    this.httpService.queryRateRepriceRuleInternalListService(this.productBean).subscribe(data => {
      let arr = []
      if (data.resultList) {

        data.resultList.forEach((item) => {
          let obj = {};
          obj['label'] = item['reprncPlnName'];
          obj['value'] = item['reprncPlnNum'];
          arr.push(obj)
        })
        let a = _.clone(arr);
        this.list3 = a
        this.list3.unshift({ label: '请选择', value: '' })
      }
    })
  }
  ngOnInit() {
    this.userform = this.fb.group({
      'pdName': new FormControl('', Validators.required),
      'pdNum': new FormControl('', Validators.required),
      'sroCode': new FormControl('', Validators.required),
      'intRatePlnNum': new FormControl('', Validators.required),
      'effDt': new FormControl('', Validators.required),
      'invalDt': new FormControl(''),
      'plnRsetNum': new FormControl(''),
      'prefPlnNum': new FormControl(''),
      'reprncPlnNum': new FormControl(''),
      'intRateLmtTpCode': new FormControl('', Validators.required),
      'hghstIntRateLmt': new FormControl(''),
      'mnIntRateLmt': new FormControl(''),
      'txInf': new FormControl('', [Validators.maxLength(42)]),
    });

    if (this.productBean.intRateLmtTpCode == null) {
      this.isRed = false;
      this.show = true;
    }


    this.old = this.inValue['effDt'];
    this.old1 = this.inValue['intRatePlnNum'];
    this.old2 = this.inValue['pdNum'];
    this.old3 = this.inValue['sroCode'];
    // 查询方法
    let param = {
      pdNum: this.inValue['pdNum'],
      intRatePlnNum: this.inValue['intRatePlnNum'],
      sroCode: this.inValue['sroCode'],
      effDt: this.inValue['effDt'],
      id: this.inValue['id']
    }
    this.httpService.queryProductWithRateByIdService(param).subscribe(data => {
      if (data.resultMap) {
        this.productBean = data.resultMap;
        this.disableText();
        this.plnRset(this.productBean.sroCode);
      }
      this.effDt = new Date(this.productBean['effDt']);
      this.invalDt = new Date(this.productBean['invalDt']);
    }, err => {
    })

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //校验上限阈值
  checkInterest() {
    if (this.productBean.hghstIntRateLmt == '') {
      this.show = false
      return false;
    }

    if (this.productBean.intRateLmtTpCode == '1' || this.productBean.intRateLmtTpCode == '2') {
      if (this.decimalInt(null, this.productBean.hghstIntRateLmt)) {
        return true;
      }
      return false;
    }
    return false;
  }

  checkRate() {
    if (this.productBean.hghstIntRateLmt == '') {
      this.show = false;
      return false;
    }

    if (this.productBean.intRateLmtTpCode == '3') {
      if (this.decimalInteger(null, this.productBean.hghstIntRateLmt)) {
        return true;
      }
      return false;
    }
    return false;
  }

  //校验下限阈值
  checkInter() {
    if (this.productBean.mnIntRateLmt == '') {
      this.show = false;
      return false;
    }

    if (this.productBean.intRateLmtTpCode == '1' || this.productBean.intRateLmtTpCode == '2') {
      if (this.decimalInt(null, this.productBean.mnIntRateLmt)) {
        return true;
      }
      return false;
    }
    return false;
  }

  checkRa() {
    if (this.productBean.mnIntRateLmt == '') {
      this.show = false;
      return false;
    }

    if (this.productBean.intRateLmtTpCode == '3') {
      if (this.decimalInteger(null, this.productBean.mnIntRateLmt)) {
        return true;
      }
      return false;
    }
    return false;
  }
  ngOnChanges() {
  }
  // //获取日期组件的数据
  // showData() {
  //   this.productBean.effDt = this.commfunc.transDate(this.effDt)
  //   this.productBean.invalDt = this.commfunc.transDate(this.invalDt)
  // }

  // 控制阈值类型
  disableText() {
    if (this.productBean.intRateLmtTpCode == "" || this.productBean.intRateLmtTpCode == undefined) {
      this.productBean['hghstIntRateLmt'] = '';
      this.productBean['mnIntRateLmt'] = '';
      this.isRed = false;
      this.show1 = true
    } else if (this.productBean.intRateLmtTpCode == '9') {
      this.productBean['hghstIntRateLmt'] = '';
      this.productBean['mnIntRateLmt'] = '';
      this.isRed = false;
      this.show1 = true
    } else {
      this.isRed = true;
      this.show1 = false
    }
  };

  plnRset(value) {
    if (value == "A5") {
      this.show2 = true;
      this.productBean.plnRsetNum = '';
      this.productBean.prefPlnNum = '';
      this.productBean.reprncPlnNum = ''
    } else {
      this.show2 = false
    }

  }

  //保存
  submit() {
    if (this.isDisable) {
      return;
    }
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    // 阀值类型下拉控制
    if (this.productBean.intRateLmtTpCode == "1" || this.productBean.intRateLmtTpCode == "2") {
      if (!this.productBean.hghstIntRateLmt || !this.productBean.mnIntRateLmt) {
        this.isRed = true;
        return;
      } else {
        if (this.decimalInt(null, this.productBean.mnIntRateLmt) || this.decimalInt(null, this.productBean.hghstIntRateLmt)) {
          return
        }
      }
    } else if (this.productBean.intRateLmtTpCode == "3") {
      if (!this.productBean.hghstIntRateLmt || !this.productBean.mnIntRateLmt) {
        this.isRed = true;
        return;
      } else {
        if (this.decimalInteger(null, this.productBean.mnIntRateLmt) || this.decimalInteger(null, this.productBean.hghstIntRateLmt)) {
          return
        }
      }
    }

    //时间戳转为十位
    // this.productBean.intRatePlnName = this.codeValue(this.productBean.intRatePlnName,this.list);
    this.productBean['oldEffDt'] = this.old;
    this.productBean['oldIntRatePlnNum'] = this.old1;
    this.productBean['oldPdNum'] = this.old2;
    this.productBean['oldSroCode'] = this.old3;
    let aa = this.commfunc.transDateN(this.effDt);//生效日期
    this.productBean.effDt = aa;
    this.productBean.invalDt = this.commfunc.transDateN(this.invalDt);//失效日期
    this.isDisable = true;
    this.httpService.modifyProductWithRateService(this.productBean).subscribe(data => {
      // this.productBean = data.returnCode;
      if (data.returnCode.code == "success") {
        this.outValue.emit(data.returnCode.message);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.isDisable = false;
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "服务异常" });
      this.isDisable = false;
    });
  }
  //s鼠标事件，展示全部内容
  //  mouseEnter(p, el) {
  //   let clientH = document.body.offsetHeight; //body高度
  //   let clientW = document.body.offsetWidth; //body宽度
  //   let docH = document.body.scrollHeight; //浏览器高度
  //   let docW = document.body.scrollWidth; //浏览器宽度
  //   let bgW = clientW > docW ? clientW : docW; //取有效宽
  //   let bgH = clientH > docH ? clientH : docH; //取有效高

  //   let top = el.target.getBoundingClientRect().top;
  //   let left = el.target.getBoundingClientRect().left;
  //   let blackBg = document.createElement("div");
  //   blackBg.id = "_detailDialog_";
  //   blackBg.style.position = "absolute";
  //   blackBg.style.zIndex = "99999";
  //   blackBg.style.width = el.target.offsetWidth +30+ "px";
  //   blackBg.style.opacity = "1";
  //   blackBg.style.backgroundColor = "white";
  //   blackBg.style.borderRadius = "5px";
  //   blackBg.style.padding = "5px";
  //   blackBg.style.border = "2px solid black";
  //   blackBg.style.wordWrap = 'break-word';

  //   if ((bgW - left - el.target.offsetWidth) > el.target.offsetWidth + 8) {
  //     blackBg.style.top = el.clientY + 8 + 'px';
  //     blackBg.style.left = el.clientX + 8 + 'px';
  //   } else if ((bgW - left - el.target.offsetWidth) < el.target.offsetWidth) {
  //     blackBg.style.top = (el.clientY + 16) + 'px';
  //     blackBg.style.left = (el.clientX - 16 - el.target.offsetWidth) + 'px';
  //   }

  //   if (p) {
  //     blackBg.innerHTML = p;
  //     document.body.appendChild(blackBg);
  //     this.ce.detectChanges();
  //   }

  // }

  // mouseLeave(el) {
  //   document.getElementById('_detailDialog_').remove();

  // }
  //返回
  back() {
    this.outValue.emit(false);
  }
  doChose() {
    this.productBean.intRatePlnNum = this.productBean.intRatePlnName
  }
  //选择产品模板
  choose() {
    this.headerTitle = '可售产品列表';
    this.showModel = '3';
    this.display = true;
  }
  //选择产品模板
  chooseCall(parm) {
    if (parm) {
      this.productBean.pdName = parm[0].productName;
      this.productBean.pdNum = parm[0].productId;
      this.display = parm[1];
    } else {
      this.display = parm;
    }
  }
}
