import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { ProductBean } from './../bean/product.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// import { IsScene, IsFazhi } from './../../constant/codeValue';
import * as _ from 'lodash';
import { Message } from 'primeng/primeng';
import { ProductManageHttpService } from '../../../../product-manage/http/product-manage.http.service';

@Component({
  selector: 'app-chooser-product',
  templateUrl: './chooser-product.component.html',
  styleUrls: ['./chooser-product.component.scss'],
  providers: [ConfirmationService, ProductManageHttpService]
})
export class ChooserProductComponent implements OnInit {
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //传入详情的值
  inValueCode: string;
  //提示信息
  msgs: Message[] = [];
  product_list: any = []
  //分页
  pageSize: any = 10;
  pageNum: any = 1;
  valueListOne: any = [];
  total: number;
  product: any;
  currentPage: number = 1;
  first: number = 0;//分页控制
  productBean: ProductBean = new ProductBean();
  constructor(
    public httpService: ProductManageHttpService, ) { }
  ngOnInit() {
    this.productBean.pageSize = 10;
    this.productBean.pageNum = 1;
    this.first = 0;
    this.query()
  }
  query1(){
    this.productBean.pageNum = 1;
    this.query();
  }
  //查询
  query() {
    this.httpService.findAvailableProductList(this.productBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.product_list = data.resultList;
        this.total = data.total;
        this.productBean.pageNum = data.pageNum;
        this.productBean.pageSize = data.pageSize;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.productBean.pageSize = event.rows;
    //当前页
    this.currentPage  = event.page + 1;
    this.productBean.pageNum = this.currentPage;
    this.first = (this.currentPage - 1) * this.productBean.pageSize;
    //调用查询的方法
    this.query();
    // this.productBean.pageNum = 1;
  }
  //选择
  choose(car) {
    this.product = car;
  }
  //确定
  mainSeve() {
    let patm = []
    if (this.product) {
      patm.push(this.product);
    } else {
      patm.push({});
    }
    patm.push(false);
    this.outValue.emit(patm);
  }
  //取消
  goBack() {
    this.outValue.emit(false);
  }
  // 重置
  resetClick() {
    this.productBean = new ProductBean();
    this.first = 0;
    this.currentPage = 1
  }
}
